*{
    box-sizing: border-box;
}

body{
    margin: 0;
    font-family: 'Poppins', sans-serif;
    height: 100vh;
}

nav{
    width: 100vw;
    padding: 2rem 4rem;
    box-shadow: 0 10px 10px rgb(0, 0, 0,0.1);
    display: flex;
    align-items: center;
}

nav > img{
    width: 100px;
}

.hero{
    width: 100%;
    height: 100%;
}

.hero-img{
    display: flex;
    justify-content: center;
    align-items: center;
   
    padding: 4rem 0 0 0;
}

section{
    padding: 0 4rem;
}
.hero-desc{
    display: flex;
    flex-direction: column;
    padding: 2rem 0;
}

.hero-desc h1{
    font-size: 68px;
    font-weight: 600;
    color: hsl(0,0,0);
    margin-top: -.5rem;
}

.hero-desc p{
    font-size: 28px;
    font-weight: 200;
    width: 600px;
    line-height: 2rem;
    margin-top: -1.5rem;
}

.card{
    width: 175px;
    font-size: 12px;
    position: relative;
}

.card--image{
    width: 100%;
    border-radius: 9px;
}

.card--stats{
    display: flex;
    align-items: center;
}

.card--star{
    height: 14px;
}

.gray{
    color: #918E9B;
}

.bold{
    font-weight: bold;
}

.cardContainer{
    display: flex;
    
}

.card--badge{
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}